html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  grid-template-rows: 1fr 1fr;
  grid-gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
  max-width: 1200px;
}

@media (min-width: 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (min-width: 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (min-width: 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  padding: 32px 0 16px;
  width: 200px;
  box-sizing: border-box;
  display: block;
  background-color: $bodyColor;
  border-radius: 5px;
  border: 1px solid $whiteGrayColor;

  &:hover {
    transition: 300ms;
    transform: scale(1.2);

    &:hover &__info-name {
      transition: 300ms;
      color: #34568b;
    }
  }

  &__picture {
    margin-left: 19px;
    width: 160px;
    height: 134px;
  }

  &__info {
    width: 166px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $nameTextColor;
    margin: 36px 16px 0;

    &-name {
      margin: 0;
      font-size: 12px;
      line-height: 18px;
      font-weight: 500;
    }

    &-type {
      margin: 0;
      font-size: 12px;
      line-height: 18px;
    }
  }

  &__product {
    margin: 4px 0 16px 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $subTextColor;
  }

  &__assess {
    height: 16px;
    width: 166px;
    margin-left: 16px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__review {
    font-size: 10px;
    line-height: 20px;
    width: 53px;
    margin: 0;
    padding-left: 20px;
  }

  &__price {
    width: 166px;
    display: flex;
    justify-content: space-between;
    margin: 24px 16px 16px;

    &-text {
      font-size: 12px;
      font-weight: 400;
      color: $subTextColor;
      line-height: 18px;
      margin: 0;
    }

    &-num {
      line-height: 18px;
      font-size: 16px;
      font-weight: 700;
      color: $nameTextColor;
      margin: 0;
    }
  }

  &__button {
    width: 166px;
    height: 40px;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: $blueColor;
    color: $bodyColor;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $blueColor;
  }

  &__button:hover {
    background-color: $bodyColor;
    color: $blueColor;
    border: 1px solid $blueColor;
    cursor: pointer;
  }
}
