html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.cards {
  grid-gap: 46px 48px;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  max-width: 1200px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 0 16px;
  display: block;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card:hover:hover .card:hover__info-name {
  color: #34568b;
  transition: all .3s;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin-left: 19px;
}

.card__info {
  color: #060b35;
  width: 166px;
  margin: 36px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info-name {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__info-type {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__product {
  color: #616070;
  margin: 4px 0 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__assess {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  height: 16px;
  margin-left: 16px;
  display: flex;
}

.card__review {
  width: 53px;
  margin: 0;
  padding-left: 20px;
  font-size: 10px;
  line-height: 20px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  margin: 24px 16px 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-num {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo {
  width: 100%;
  height: 100%;
}

.nav {
  align-items: center;
  display: block;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .5s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
